@import url('https://fonts.googleapis.com/css?family=Open+Sans:400,700');

:root {
  --brown: #816C68;
  --off-white: #F9F9F9;
  --gray: #4D4D4D;
}

body, html {
  width: 100%;
  height: 100%;
  margin: 0;

  background-color: var(--brown);
}

.error-message {
  background:#4D4D4D;
  color: #A69E9B;
  padding: 10px;
  border-radius: 2px;
  margin: 0px 20px;
}

.welcome,
.home,
.success {
  width: 100%;
  height: 100vh;

  color: var(--off-white);
  background-color: var(--brown);

  font-family: Open Sans, sans-serif;
}

.welcome,
.success {
  display:-ms-flexbox;
  display:flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

h1 {
  margin: 0;
  font-weight: 700;
}

.welcome .container,
.home .container {
  background: url(img/cover.jpg);
  background-size: cover;

  height: 100%;
  width: 100%;
}

.welcome .container {
  display:-ms-flexbox;
  display:flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.btn {
  background-color: var(--brown);
  color: var(--off-white);
  
  text-transform: uppercase;
  font-weight: 700;
  font-size: 15px;
  
  padding: 15px 30px;
  border: none;
  border-radius: 5px;
}

.btn:disabled {
  background-color: #6b6b6b;
  color: #868991;
}

.home .container {
/*  padding: 10px;*/
  display:-ms-flexbox;
  display:flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.home h1 {
  text-align: center;
  font-weight: 700;
}

.home p {
  width: 100%;
  text-align:center;
}

.home a{
  color: var(--gray);
}

.home .input-field{
  background-color: var(--off-white);
  font-size: 15px;

  border-style: none;
  padding:10px;
}

.home .btn-add {
  background-color: var(--brown);
  color: #F9F9F9;
  border-style: none;

  text-transform: uppercase;
  font-size: 15px;
  font-weight: 700;
  padding:10px ;
}

.home .btn-playlist-add {
  margin: 10px 0;
  text-align: center;
}

.tracklist {
  -ms-flex-positive: 1;
      flex-grow: 1;
  overflow-y: auto;
  margin: 10px 0;
}

.track-container {
  background-color: var(--off-white);
  color: var(--gray);
  margin: 10px 5%;

  width: 90%;
}

.track {
  padding: 10px;
}

.track h2 {
  margin: 0;
  font-size: 20px;
}

.track h3 {
  margin: 0;
  font-size: 16px;
  font-weight: 400;
  display: inline-block;
}

.track h4 {
  margin: 0;
  font-size: 20px;
  
  float: right;
  margin-right: 5px;
}

.track h4:hover {
  cursor: pointer;
  color: var(--brown);
}

.success .container {
  text-align: center;
}

@media screen and (min-width: 426px){
  .welcome .container {
    height: 80%;
    width: 80%;

    display:block;
  }

  .home .container {
    height: 80%;
    width: 80%;

    margin: 10%;
  }

  .welcome .container h1,
  .home .container h1 { 

    font-size: 50px;

    margin: 0;
    margin-top: -7%;
    margin-left: -7%;
  }

  .home .container h1 {
    text-align: left;
  }

  .login-container {
    height: 100%;
    width: 100%;

    display: -ms-flexbox;

    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
  }

  .track h2 {
    display: inline-block;
    margin-right: 10px;
  }

  .success .container {
    text-align: center;
    padding: 30px;
    border: 30px solid ;
    border-image-source: url(img/cover.jpg);
    border-image-slice: 100;
  }

  .success .btn {
    display: block;
    margin: auto;
    margin-top: 15px;
    border: 2px solid #242927;
  }

}

@media screen and (min-width: 768px){
  .welcome .container {
    height: 80%;
    width: 60%;
  }

  .home .container {
    height: 80%;
    width: 60%;

    margin: 10% 20%;
  }

}

@media screen and (min-width: 1025px){
  .welcome .container {
    height: 80%;
    width: 30%;
  }

  .home .container {
    height: 80%;
    width: 40%;

    margin: 5% 30%;
  }

}


